import StaticData from "./entity/StaticData"
import ApiData from "./entity/ApiData"
import _ from "lodash"

class DataProxy {

	/**
	 * 根据组件的config.proxy属性，生成数据代理
	 */
	constructor(component) {
		if (!component ||
				!component.config ||
				!component.config.proxy ||
				component.config.proxy.type === undefined ||
				component.config.proxy.type === null) {
			return
		}
		this.component = component
		switch (this.component.config.proxy.type) {
			case 1:
				this.dataSource = new StaticData(this.component)
				break
			default:
				this.dataSource = new ApiData(this.component)
				break
		}
	}

	/**
	 * 执行数据代理
	 * @returns {Promise<any>}
	 */
	do() {
		return new Promise((resolve, reject) => {
			if (this.component.config.proxy.type === 1) {
				return
			}
			this.dataSource.do()
					.then((response) => {
						let data = response
						if (response.data) {
							data = response.data
						}
						if (this.component.config.transform.prop) {
							data = _.get(data, this.component.config.transform.prop)
						}
						resolve(data)
					})
					.catch((error) => {
						reject(error)
					})
		})
	}
}

export default DataProxy
