import Base from './Base'
import axios from 'axios'
import CONFIG from '../../../config/config'

class ApiData extends Base {
	constructor(component) {
		super(component)
	}

	/**
	 * 根据proxy配置属性生成代理，并将参数进行格式化，其中参数名为空的去除
	 * 如果参数名值以冒号(:)开头，则说明引用了全局状态，此时根据状态名获取值，并写入参数对象中
	 */
	do() {
		let proxy = this.component.config.proxy
		if (!proxy || !proxy.url) {
			return new Promise((resolve, reject) => {
			})
		}
		let param = {}
		let vuex = this.component.$root.$store
		if (proxy.param && proxy.param.length) {
			for (let i = 0; i < proxy.param.length; i++) {
				// 参数名为空则不添加
				let item = proxy.param[i]
				if (!item.key) {
					continue
				}
				let val = item.value
				if (val.startsWith(':')) {
					let storeProp = val.substring(1)
					if (!storeProp) {
						continue
					}
					let storePropPath = "screen.design.interaction." + storeProp
					val = vuex.commit(CONFIG.MUTATIONS.GET, {prop: storePropPath})
				}
				param[item.key] = val
			}
		}
		// 返回Promise方便外部调用时进行回调
		return new Promise((resolve, reject) => {
			axios({
				url: proxy.url,
				method: proxy.method || CONFIG.AJAX.GET,
				data: param,
			}).then((response) => {
				resolve(response)
			}).catch((error) => {
				reject(error)
			})
		})
	}
}

export default ApiData
