import Vue from 'vue'
import Router from 'vue-router'
import Viewport from '@/views/Viewport'
import ScreenDesign from '@/views/screen/Design'
import ScreenPresentation from '@/views/screen/Presentation'

Vue.use(Router)

export default new Router({
	/*mode: 'history',*/
	routes: [{
		path: '/viewport',
		name: 'Viewport',
		component: Viewport
	}, {
		path: '/design',
		name: 'ScreenDesign',
		component: ScreenDesign
	}, {
		path: '/presentation',
		name: 'ScreenPresentation',
		component: ScreenPresentation
	}]
})
