import Vue from 'vue'
import Vuex from 'vuex'
import _ from 'lodash'
import Config from '../config/config'

Vue.use(Vuex)

const debug = process.env.NODE_ENV !== 'production'

export default new Vuex.Store({
	state: {
		screen: {
			design: {
				// 组件菜单内容
				menu: [{
					icon: 'chart',
					text: '图表',
					show: false,
					categories: [{
						text: '全部',
						icon: 'chart',
						items: [
							{text: '基本柱形图', category: 'chart', name: 'BaseBar'},
							{text: '堆叠柱形图', category: 'chart', name: 'StackBar'},
							{text: '基本饼图', category: 'chart', name: 'BasePie'},
							{text: '基本折线图', category: 'chart', name: 'BaseLine'},
							{text: '气泡图', category: 'chart', name: 'BaseScatter'},
							{text: '雷达图', category: 'chart', name: 'BaseRadar'},
							{text: '仪表盘', category: 'chart', name: 'BaseGauge'},
							{text: '关系图', category: 'chart', name: 'BaseGraph'},
							{text: '树图', category: 'chart', name: 'BaseTree'},
							{text: '桑基图', category: 'chart', name: 'BaseSankey'}
						]
					}, {
						text: '柱形图',
						icon: 'chart',
						items: [
							{text: '基本柱形图', category: 'chart', name: 'BaseBar'},
						]
					}]
				}, {
					icon: 'table',
					text: '表格',
					show: false,
					categories: [{
						text: '表格',
						icon: 'table',
						items: [
							{text: '基础表格', category: 'table', name: 'BaseTable'},
						]
					}]
				}, {
					icon: 'plugin',
					text: '组件',
					show: false,
					categories: [{
						text: '组件',
						icon: 'plugin',
						items: [
							{text: '标题', category: 'plugin', name: 'Label'},
							{text: '面板', category: 'plugin', name: 'Panel'},
							{text: '下拉列表', category: 'plugin', name: 'Select'},
							{text: '单选组', category: 'plugin', name: 'Radio'},
							{text: '日期时间', category: 'plugin', name: 'DateTimePicker'},
							{text: '时间轴', category: 'plugin', name: 'Timeline'},
							{text: '时钟', category: 'plugin', name: 'Clock'},
						]
					}]
				}, {
					icon: 'map',
					text: '地图',
					show: false,
					categories: [{
						text: '地图',
						icon: 'map',
						items: [
							{text: '流向图', category: 'map', name: 'FlowMap'},
						]
					}]
				}],
				// 储存大屏所有的联动状态
				interaction: {}
			}
		}
	},
	mutations: {
		// 组件注册和状态绑定
		[Config.MUTATIONS.REGISTER](state, payload) {
			let store = _.get(state, payload.store)
			if (store === undefined) {
				let item = {value: null, observer: []}
				if (payload.comp) {
					item.observer.push({comp: payload.comp, prop: payload.prop})
				}
				_.set(state, payload.store, item)
			} else {
				if (!store.observer || !store.observer.length) {
					store.observer = []
				}
				if (payload.comp && payload.prop) {
					store.observer.push({comp: payload.comp, prop: payload.prop})
					_.set(payload.comp, payload.prop, store.value)
				}
			}
		},
		// 状态修改
		[Config.MUTATIONS.MODIFY](state, payload) {
			let store = _.get(state, payload.store)
			if (store === undefined) {
				return
			}
			_.set(state, payload.store + '.value', payload.value)
			if (!store.observer || !store.observer.length) {
				return
			}
			for (let i = 0; i < store.observer.length; i++) {
				let ob = store.observer[i]
				if (_.get(ob.comp, ob.prop) === payload.value) {
					continue
				}
				_.set(ob.comp, ob.prop, payload.value)
			}
		},
		// 状态获取
		[Config.MUTATIONS.GET](state, payload) {
			return _.get(state, payload.prop + '.value')
		},
		// 状态保存
		[Config.MUTATIONS.COMMIT](state, payload) {
			let store = _.get(state, payload.store)
			if (store === undefined) {
				return
			}
			_.set(state, payload.store, payload.value)
		},
		// 状态删除
		[Config.MUTATIONS.DELETE](state, payload) {
			let store = _.get(state, payload.store)
			if (store === undefined) {
				return
			}
			if (!store.observer.length) {
				_.unset(state, payload.store)
			}
		},
		// 组件注销
		[Config.MUTATIONS.UNREGISTER](state, payload) {
			let comp = payload.comp
			let store = comp.config.store
			for (let key in store) {
				let obj = _.get(state.screen.design.interaction, key)
				let observer = obj.observer
				for (let i = observer.length - 1; i >= 0; i--) {
					let ob = observer[i]
					if (ob.comp == comp) {
						observer.splice(i, 1)
					}
				}
			}
		}
	}
})
