import CONFIG from '../../../config/config'

class Base {

	/**
	 * 遍历所有参数，其中以冒号(:)开头的变量是引用全局状态，
	 * 将全局状态存储到config.store属性中，并当前组件注册到vuex store，
	 * 订阅该全局状态的变更消息，未来当全局状态变更时，分发消息到当前组件的config.store中
	 * 当前组件通过监听config.store属性的变化，来更新组件状态
	 */
	constructor(component) {
		this.component = component
		let proxy = this.component.config.proxy
		if (!proxy.param || !proxy.param.length) {
			return
		}
		let store = {}
		for (let i = 0; i < proxy.param.length; i++) {
			// 参数名或值为空则跳过
			let item = proxy.param[i]
			if (!item.key || !item.value) {
				continue
			}
			// 获取vuex
			let vuex = this.component.$root.$store
			// 参数值如果是:开头，表示绑定了全局状态
			let val = item.value
			if (val.startsWith(':')) {
				let storeProp = val.substring(1)
				if (!storeProp) {
					continue
				}
				// 获取vuex中的属性路径
				let storePropPath = "screen.design.interaction." + storeProp
				// 在store中储存全局状态名称和值
				store[storeProp] = vuex.commit(CONFIG.MUTATIONS.GET, {prop: storePropPath})
				// 订阅全局状态的修改消息
				vuex.commit(CONFIG.MUTATIONS.REGISTER, {
					// 绑定哪个vuex状态
					store: storePropPath,
					// 当前的绑定对象
					comp: this.component,
					// 当vuex状态变更时，应该修改绑定对象的哪个属性
					prop: "config.store." + storeProp
				})
			}
		}
		this.component.config.store = store
	}

	do() {
		throw new Error("DataSource错误：未声明代理方法do")
	}
}

export default Base
